@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700;800&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html, body {
  box-sizing: border-box;
  height: 100%;
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.bigButton{background-color:#FFBD12;border-radius:16px;height:52px;font-size:16px;font-weight:800;width:auto}

.footer{flex-shrink:0}

.header{height:100px}

.mascot img{width:100%}

.message{min-height:88px;position:relative;font-weight:800;font-size:16px;padding:15px;border-radius:20px}.message .rectangle{width:0;height:0;border-left:15px solid transparent;border-right:15px solid transparent;border-top:30px solid white;position:absolute;bottom:-20px}

.welcome-page{height:100vh;background-color:#08695D}

